          animation:blinkingText 1.2s infinite;
  }

.dot {
 
  height: 50px;
  width: 50px;
  background-color:black;
  border-radius: 50%;
  /* display: inline-block; */
  position: absolute;
  top: 50vh;
  left: 47.8%;
  /* margin-left: auto;
  margin-right: auto; */
   opacity: 0.01;
   padding: 0px;
   text-align: center;  
}
.dot{
  -webkit-transition: all 1.8s ease-in-out;
  transition: all 1.8s ease-in-out;
  overflow: hidden;
}


#container:hover > .dot{ -webkit-transform: scale(10); transform: scale(10); opacity: 1; }

